.buttons {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.button {
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 12px 24px;
  font-size: 18px;
  font-weight: bold;
  transition: all .3s;
  display: inline-block;
}

.start {
  color: #fff;
  background-color: #28a745;
  margin-right: 16px;
}

.start:hover {
  background-color: #218838;
}

.stop {
  color: #fff;
  background-color: #dc3545;
}

.stop:hover {
  background-color: #c82333;
}

.start:disabled, .stop:disabled {
  opacity: .5;
  cursor: default;
}

