.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.button {
  display: inline-block;
  padding: 12px 24px;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.start {
  background-color: #28a745;
  color: #fff;
  margin-right: 16px;
}

.start:hover {
  background-color: #218838;
}

.stop {
  background-color: #dc3545;
  color: #fff;
}

.stop:hover {
  background-color: #c82333;
}

.start:disabled,
.stop:disabled {
  opacity: 0.5;
  cursor: default;
}
